.video {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.voice-btn {
  width: 68px;
}
#investmentAmount {
  max-width: 345px;
}
#investTab .nav-link {
  color: #000;
}
#investTab .nav-link:hover,
#investTab .nav-link.active {
  color: #00000098;
}
@media (max-width: 576px) {
  .investment-table {
    overflow-x: scroll;
  }
  .investment-table .btn {
    min-width: 94px;
  }
  .more-invest {
    overflow-x: scroll;
  }
}
@media (max-width: 362px) {
  #investmentAmount {
    max-width: 159px;
  }
}
.small-modal {
  max-width: 362px;
}
